import { useState } from 'react';
import './App.css'
import Header from './components/Header';
import Counter from './Counter';
import Employee from './Employee';
import Kounter from './Kounter';
function App() {

  const [count,setCount]=useState(0)
  const [state,setState]=useState(true)
  const data = 'asdhhgdsjnmjf'
  const data1='anaz'
  // let count=0;
  function addCount(){
    setCount(count+1)    
    // count=count+1;
    // console.log(count)
  }
  let emp = [
    {name:'anz', age:24},
    {name:'ans',age:14},
  ]
  return (
    <div>
    <Header data={data1}/>
      {/* <h1 className="hello" style={{backgroundColor:'red'}}>Hello world</h1> */}
      <p>This is the sample description  {data}</p>
      {/* <h1>Counter:{count}</h1> */}
      <button onClick={addCount}>Add</button>
      <Counter title='1st Counter' count={count}/>
      <Counter title='2nd Counter' count={count}/>
      {
        emp.map((obj,index)=>{
          return(
            // <Employee key={index} name={obj.name} age={obj.age}/>
            <Employee key={index} {...obj}/>
          )                      
        })
      }
      <h2 onClick={()=>setState(!state)}>show/hide</h2>
     {/* like if statements,if state is true display this component or not. */}
      {
        state ? <Kounter/> : null
      }
    </div>
  );
}

export default App;