import React,{useState,useEffect} from 'react'

function Kounter() {
    const [adder,setAdd]=useState(0)
    useEffect(() => {
        console.log("Mounting...")
        console.log('updating.. '+adder);
        // return () => {
        //     console.log("Unmounting...")
        // }
    }, [adder])
   
    return (
        <div>
            <button onClick={()=>setAdd(adder+1)}>find</button>
            <h1>you saw me {adder} times</h1>
        </div>
    )
}

export default Kounter
